import { net } from '../api/net.js'

const headers = function(token) {
  let res = {}
  
  if (token) {
    res['Authorization'] = 'Bearer ' + token
  }
  return res
}

const builParam = function(condition) {
  let res = '?'

  if (JSON.stringify(condition) == '{}') {
    
  } else {

    for (const k in condition) {
      const item = k + '=' + condition[k] + '&'
      res += item
    }

  }

  return res
}

const get = function(url, token, condition = {}) {
  
  url = url + builParam(condition)

  return net({
    url,
    method: 'get',
    headers: headers(token)
  })
}

const post = function(url, token, data) {
  return net({
    url,
    method: 'post',
    data,
    headers: headers(token)
  })
}

const put = function(url, token, data) {
  
  return net({
    url,
    method: 'put',
    data,
    headers: headers(token)
  })
}

export default {
  get,
  post,
  put,
}


