
const Buys = () => import('@/pages/Buys/Buys')
const BuysEdit = () => import('@/pages/Buys/Edit/BuysEdit')

export default {
    
    path: '/home/buys',
    component: Buys,
    
    redirect:'/home/buys/edit',
    children: [
        {
            path: '/home/buys/edit',
            component: BuysEdit,
        }
    ]
}


