import Vue from 'vue'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'

import "@mdi/font/css/materialdesignicons.css";
import "material-design-icons-iconfont/dist/material-design-icons.css";

import "font-awesome/css/font-awesome.min.css";

import conf from '@/conf'
import colors from "vuetify/es5/util/colors"

const theme = conf.theme

Vue.use(Vuetify)

const opts = {
    icons: {
        iconfont: 'mdiSvg'
    },
    // lang: { current: store.state.translation.locale, },
    rtl: false, // store.state.scheme.theme.rtl,
    theme: {
      dark: theme.dark,
      default: "light",
      disable: false,
      options: {
        cspNonce: undefined,
        customProperties: true,
        minifyTheme: undefined,
        themeCache: undefined,
      },
      themes: {
        light: {
          primary: theme.primary,
          secondary: theme.secondary.base,
          accent: theme.accent.base,
          error: colors.red.accent4,
          info: colors.blue.lighten1,
          success: colors.green.accent4,
          warning: colors.amber.darken2,
          background: '#f8f9fa', // "#F2F3F7", // #ECF0F3
          neu: {
            darken1: "#DDE4EF", // rgba(210, 218, 230, 0.6)
            lighten1: "#FFFFFF", // rgba(255, 255, 255, 0.6)
          },
          primaryBgText: theme.primaryBgText,
          secondaryBgText: theme.secondaryBgText,
        },
        dark: {
          primary: theme.primary,
          secondary: theme.secondary.base,
          accent: theme.accent.darken1,
          error: colors.red.accent4,
          info: colors.blue.lighten1,
          success: colors.green.accent4,
          warning: colors.amber.darken2,
          background: '#1d2327', // "#292D32",
          neu: {
            darken1: '#1d2327', // "#1d1f23", // rgba(210, 218, 230, 0.6)
            lighten1: "#1d2327", // rgba(255, 255, 255, 0.6)
          },
          primaryBgText: theme.primaryBgText,
          secondaryBgText: theme.secondaryBgText,
        },
      },
    },
  }

export default new Vuetify(opts)


