<template>
    <div></div>
</template>

<script>
    export default {
        name: '',
        data() {
            return {
                
            }
        },
        mounted() {
        },
        methods: {
            async init() {
                const user_unit = await this.loadingItems()
                this.$store.commit('saveUnitUser', user_unit)

                const products = await this.productsLoading()
                this.$store.commit('saveProducts', products)

                console.log('Loading Completed!')
            },

            async productsLoading() {
                let prod = await this.conn.get(this.api.products, this.$store.state.token)

                if (prod) {
                    prod = prod.map(e => {
                        return {
                            id: e.id,
                            named: e.named,
                            price: e.price,
                            pv: e.pv
                        }
                    })
                }
                
                return prod
            },

            async loadingUser() {
                const res = await this.conn.get(this.api.users, this.$store.state.token)

                if (res) {
                    
                }

                return res
            },
            async loadingBuys() {
                let res = await this.conn.get(this.api.buys, this.$store.state.token)

                if (res) {
                    res = res.map(e => {
                        return {
                            buy_time: e.buy_time,
                            num: e.num,
                            pv: e.product.pv,
                            user: e.users_permissions_user.id
                        }
                    })
                }
                
                return res
            },
            async loadingItems() {
                let items = [ ]

                const users = await this.loadingUser()
                const buys = await this.loadingBuys()

                if (users) {
                    users.map(e => {

                        let res = { }
                        
                        res.name = e.username
                        res.is_new = e.is_new
                        res.unid = e.unid
                        res.sponser_id = e.sponser_id
                        res.enroller_id = e.enroller_id
                        res.id = e.id
                        res.buys = [ ]

                        buys.map(buy => {
                            if (buy.user == e.id) {
                                res.buys.push(buy)
                            }
                        })

                        items.push(res)
                    })
                }

                return items
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


