
const Ui = () => import('@/pages/i/Ui')
const UiElement = () => import('@/pages/i/Ui/UiElement')

export default {
    
    path: '/home/ui',
    component: Ui,
    
    redirect:'/home/ui/element',
    children: [
        {
            path: '/home/ui/element',
            component: UiElement,
        }
    ]
}


